*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.register_background {
    position: absolute;
    top: 0;
    background-color: #f0f0f1;
    height: 100vh;
    width: 100vw;
}
/* Container */
.container{
    display: flex;
    height: 500px;
    width: 800px;
    /*border: 1px solid black;*/
    margin: auto;
    margin-top: 50px;
    box-shadow: 15px 15px 20px black;
}

/* Login Form */
.form{
    display: flex;
    flex-direction: column;
    width: 50%;
    align-items: center;
    background-color: whitesmoke;
}
.form h2{
    font-size: 3rem;
    font-family: Impact, Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    margin: 40px;
}
.box{
    padding: 12px;
    width: 65%;
    margin: 15px;
    border: 1px solid black;
    outline: none;
    border-radius: 20px;
}
#submit{
    padding: 12px 30px;
    width: 40%;
    margin-top: 40px;
    background-color: black;
    color: white;
    font-weight: bold;
    border: none;
    outline: none;
    border-radius: 20px;
}
#submit:hover{
    cursor: pointer;
    background-color: #750000;
}
.form a{
    text-decoration: none;
    color: black;
    margin-top: 20px;
}
.form a:hover{
    color: #fd4e4e;
}

/* Right Side */
.side{
    display: flex;
    justify-content: center;
    width: 50%;
    align-items: center;
    background-color: whitesmoke;
}
.side img{
    width: 380px;
    height: 450px;
}

